import React, { useState, useContext } from "react";
import {
  MDBNavbar,
  MDBNavbarBrand,
  MDBNavbarNav,
  MDBNavbarToggler,
  MDBCollapse,
  MDBNavItem,
} from "mdbreact";
import { FirebaseContext } from "../../firebase/context";
import Swal from "sweetalert2";

const TopNavigation = () => {
  const [collapse, setCollapse] = useState(false);
  const { firebase } = useContext(FirebaseContext);

  const onClick = () => {
    setCollapse(!collapse);
  };

  return (
    <MDBNavbar className="flexible-navbar" light expand="md" scrolling>
      <MDBNavbarBrand href="/">
        <strong>React Admin</strong>
      </MDBNavbarBrand>
      <MDBNavbarToggler onClick={onClick} />
      <MDBCollapse isOpen={collapse} navbar>
        <MDBNavbarNav right>
          <MDBNavItem>
            <a className="nav-link Ripple-parent" href="/dashboard">
              Dashboard
            </a>
          </MDBNavItem>
          <MDBNavItem>
            <a className="nav-link Ripple-parent" href="/perfil">
              Perfil
            </a>
          </MDBNavItem>
          <MDBNavItem>
            <a className="nav-link Ripple-parent" href="/clientes">
              Clientes
            </a>
          </MDBNavItem>
          <MDBNavItem>
            <a className="nav-link Ripple-parent" href="/productos">
              Productos
            </a>
          </MDBNavItem>
          <MDBNavItem>
            <a className="nav-link Ripple-parent" href="/facturas">
              Cotizaciones
            </a>
          </MDBNavItem>
          <MDBNavItem>
            <a
              href="#!"
              className="nav-link Ripple-parent red-text font-weight-bold"
              onClick={() =>
                Swal.fire({
                  title: "Desea cerrar la sesion?",
                  icon: "warning",
                  showCancelButton: true,
                  confirmButtonColor: "#3085d6",
                  cancelButtonColor: "#d33",
                  confirmButtonText: "Si, cerrar sesion!",
                }).then((result) => {
                  if (result.value) {
                    firebase.cerrarSesion();
                  }
                })
              }
            >
              Cerrar Sesion
            </a>
          </MDBNavItem>
        </MDBNavbarNav>
      </MDBCollapse>
    </MDBNavbar>
  );
};

export default TopNavigation;
